<template>
  <div class="vue-googlemap-container">
    <div class="vue-googlemap"></div>
    <slot></slot>
  </div>
</template>

<script>
import googlemapLoader, { instance } from "../googlemapLoader.js";
export default {
  name: "vue-googlemap",
  data() {
    return {};
  },
  props: {
    center: {
      type: Array,
      required: true
    },
    zoom: {
      type: Number,
      default: 13
    },
    controls: {
      type: Array,
      default: () => [
        "zoom",
        "mapType",
        "scale",
        "streetView",
        "rotate",
        "fullscreen"
      ]
    },
    controlsDisabeled: Boolean,
    events: Object
  },
  mounted() {
    this.createMap();
  },
  methods: {
    createMap() {
      const { center, zoom, controls, controlsDisabeled } = this;
      const options = {
        center: { lat: center[0], lng: center[1] },
        zoom
      };
      if (controlsDisabeled) {
        Object.assign(options, { disableDefaultUI: true });
      } else {
        const controlsArray = [
          "zoom",
          "mapType",
          "scale",
          "streetView",
          "rotate",
          "fullscreen"
        ];
        controlsArray.forEach(c => {
          if (controls.indexOf(c) !== -1) {
            Object.assign(options, { [`${c}Control`]: true });
          } else {
            Object.assign(options, { [`${c}Control`]: false });
          }
        });
      }
      instance.load().then(() => {
        this.$map = new google.maps.Map(
          this.$el.querySelector(".vue-googlemap"),
          options
        );
        console.log(this.$children);
        if (this.$children.length !== 0)
          this.$children.forEach(c => {
            c.$emit("mapReady", this.$map);
          });
        this.registerEvents();
      });
    },
    registerEvents() {
      const events = this.events;
      if (!events) return;
      for (let eventName in events) {
        if (eventName) this.$map.addListener(eventName, events[eventName]);
      }
    }
  }
};
</script>

<style>
.vue-googlemap-container {
  height: 100%;
}
.vue-googlemap {
  height: 100%;
}
</style>



