<template>
  
</template>

<script>
export default {
  name: "vue-googlemap-marker",
  data() {
    return {};
  },
  props: {
    position: Array
  },
  mounted() {
    this.$map = this.$map || this.$parent.$map;
    if (this.$map) {
      this.createMarker();
    } else {
      this.$on("mapReady", map => {
        this.$map = map;
        this.createMarker();
      });
    }
  },
  methods: {
    createMarker() {
      new google.maps.Marker({
        position: { lat: this.position[0], lng: this.position[1] },
        map: this.$map
      });
    }
  }
};
</script>


